<template>
  <nav id="nav">
    <b-button variant="dark" id="menuToggle" @click="toggleMenu">
      <!-- &#x2630; -->
      <i class="fas fa-bars fa-lg"></i>
    </b-button>
    <div id="logoDiv">
      <img id="logo" src="../assets/LayoutOficialSF-1.png" alt />
    </div>

    <div id="menu" class="hideMenu">
      <div class="menu-item" id="menu-left">
        <router-link to="/">Início</router-link>
        <div class="submenu" id="seguros">
          <a href="#!" @click="toggle('div#list1')">
            Seguros
            <i class="arrow down"></i>
          </a>

          <div id="list1" class="hide">
            <b-list-group class="content">
              <b-list-group-item>
                <router-link to="/">Automóveis</router-link>
              </b-list-group-item>
              <b-list-group-item>
                <router-link to="/">Casco Maritmo</router-link>
              </b-list-group-item>
              <b-list-group-item>
                <router-link to="/">Equipamentos</router-link>
              </b-list-group-item>
              <b-list-group-item>
                <router-link to="/">Residencial</router-link>
              </b-list-group-item>
              <b-list-group-item>
                <router-link to="/">Responsabilidade Civil</router-link>
              </b-list-group-item>
              <b-list-group-item>
                <router-link to="/">Saúde</router-link>
              </b-list-group-item>
              <b-list-group-item>
                <router-link to="/">Vida em Grupo</router-link>
              </b-list-group-item>
            </b-list-group>
          </div>
        </div>

        <div class="submenu" id="servicos">
          <a href="#!" @click="toggle('div#list2')">
            Serviços
            <i class="arrow down"></i>
          </a>
          <div id="list2" class="hide">
            <b-list-group class="content">
              <b-list-group-item>
                <router-link to="/">Pedido de Cotação</router-link>
              </b-list-group-item>
              <b-list-group-item>
                <router-link to="/">Seguradoras</router-link>
              </b-list-group-item>
            </b-list-group>
          </div>
        </div>
      </div>

      <div class="menu-item" id="menu-right">
        <router-link to="/">Sobre</router-link>
        <router-link to="/">Fale Conosco</router-link>
        <router-link to="/localizacao">Localização</router-link>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  methods: {
    toggle(selector) {
      var element = document.querySelector(selector);
      if (element.className == "hide") element.className = "show";
      else element.className = "hide";
    },
    toggleMenu() {
      var menu = document.querySelector("div#menu");
      if (menu.className == "hideMenu") menu.className = "showMenu";
      else {
        menu.className = "hideMenu";
        // hide submenus also
        document.querySelector("div#list1").className = "hide";
        document.querySelector("div#list2").className = "hide";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$navy: #252d40;

// DROPDOWN ARROW
.arrow {
  border: solid black;
  border-width: 0 1.3px 1.3px 0;
  display: inline-block;
  padding: 3px;
}
.down {
  margin-bottom: 3px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

@media screen and (max-width: 900px) {
  .hide {
    .list-group {
      display: none;
    }
  }

  .hideMenu {
    width: 0;
    & * {
      display: none;
    }
  }
  .showMenu {
    width: 100%;
  }

  #menuToggle {
    position: absolute;
    right: 15px;
    top: 15px;
    z-index: 2;
  }
  #nav {
    font-size: 1.5em;
    flex-direction: column;

    #logoDiv {
      order: 1;
      #logo {
        height: 70px;
      }
    }
    #menu {
      position: absolute;
      right: 0;
      top: 0;
      padding-top: 80px;
      z-index: 1;
      height: 100vh;
      background-color: rgba($color: #ffffff, $alpha: 0.92);
      transition: all 0.4s ease-out;
    }
    .menu-item {
      display: flex;
      flex-direction: column;

      a {
        color: $navy;
      }
      & > * {
        padding: 10px;
      }
      .submenu {
        .list-group-item {
          background-color: rgba($color: #ffffff, $alpha: 1);
        }
      }
    }
  }
}
@media screen and (min-width: 901px) {
  #menuToggle {
    display: none;
  }
  #nav {
    font-size: 0.8em;
    position: relative;
    padding: 0 30px;

    #menu {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .menu-item {
      display: flex;

      & > * {
        padding: 20px 20px;
        display: flex;
        justify-content: center;
        flex-direction: column;
      }
      .submenu {
        position: relative;
        .content {
          position: absolute;
          top: 40px;
          visibility: hidden;
          left: 10px;
          min-width: 200px;
          z-index: 0;
          text-align: start;
          transition: top 0.4s ease-out;
          .list-group-item {
            background-color: rgba($color: #ffffff, $alpha: 0.9);
            a {
              color: $navy;
            }
            &:hover {
              background-color: #eeeeee;
            }
          }
        }
        &:hover .content {
          top: 59px;
          visibility: visible;
        }
      }
    }
    a {
      color: $navy;
      display: block;

      &.router-link-exact-active {
        text-decoration: none;
      }
    }

    #logoDiv {
      position: absolute;
      transform: translateX(-50%);
      left: 50%;
      top: 0;
      display: block;
      #logo {
        max-height: 60px;
      }
    }
  }
}
#nav {
  background-color: rgba($color: #ffffff, $alpha: 0.5);
}

a:hover {
  text-decoration: none;
}
</style>